@import '../common/styles/sass/variables';
@import "../common/styles/sass/mixins";

.footerBlock {
  background-color: $bgSecondColor;

  .container {

    flex-direction: column;
    justify-content: space-around;

    .sqareBlock {
      margin: 30px 0;
      width: 271px;
      height: 50px;
      display: flex;
      justify-content: space-between;

      .square {
        height: 50px;
        width: 50px;
        background-color: #949d96;
      }
    }

    .rights {
      @include text($textColor, #898989, 12px, 1.2)
    }

    .round {
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      width: 2.5em;
      height: 2.5em;
      border: 1px solid #999999;
      border-radius: 50%;

      &:hover {
        border: 1px solid #292929;
      }
    }
  }

}






